import * as Amber from'./amber.js'
import * as Barbara from'./barbara.js'
import * as Beidou from'./beidou.js'
import * as Bennett from'./bennett.js'
import * as Chongyun from './chongyun.js'
import * as Diluc from './diluc.js'
import * as Fischl from'./fischl.js'
import * as Jean from'./jean.js'
import * as Kaeya from'./kaeya.js'
import * as Keqing from'./keqing.js'
import * as Klee from'./klee.js'
import * as Lisa from'./lisa.js'
import * as Mona from'./mona.js'
import * as Ningguang from'./ningguang.js'
import * as Noelle from'./noelle.js'
import * as Qiqi from'./qiqi.js'
import * as Razor from'./razor.js'
import * as Sucrose from'./sucrose.js'
import * as TravelerAnemo from'./traveleranemo.js'
import * as TravelerGeo from'./travelergeo.js'
import * as Venti from'./venti.js'
import * as Xiangling from'./xiangling.js'
import * as Xingqiu from'./xingqiu.js'
import * as Childe from'./childe.js'

export default function GetCharacterData(name) {
    switch(name){
        case 'Childe':
            return Childe.default
        case 'Xingqiu':
            return Xingqiu.default
        case 'Xiangling':
            return Xiangling.default
        case 'Venti':
            return Venti.default
        case 'Traveler (Anemo)':
            return TravelerAnemo.default
        case 'Traveler (Geo)':
            return TravelerGeo.default
        case 'Sucrose':
            return Sucrose.default
        case 'Razor':
            return Razor.default
        case 'Qiqi':
            return Qiqi.default
        case 'Noelle':
            return Noelle.default
        case 'Ningguang':
            return Ningguang.default
        case 'Mona':
            return Mona.default
        case 'Klee':
            return Klee.default
        case 'Keqing':
            return Keqing.default
        case 'Diluc':
            return Diluc.default
        case 'Chongyun':
            return Chongyun.default
        case 'Lisa':
            return Lisa.default
        case 'Amber':
            return Amber.default
        case 'Kaeya':
            return Kaeya.default
        case 'Fischl':
            return Fischl.default
        case 'Bennett':
            return Bennett.default
        case 'Barbara':
            return Barbara.default
        case 'Beidou':
            return Beidou.default
        case 'Jean':
        default:
            return Jean.default
    }
}
